import Typography from 'typography';

const typography = new Typography({
    baseFontSize: '17px',
    headerFontFamily: [
        'Noto Sans Bold',
        'Noto Sans SemiBold',
        'Noto Sans Medium',
        'Noto Sans Light',
        'san-serif'
    ],
    bodyFontFamily: [
        'Noto Sans Light',
        'Noto Sans Regular',
        'san-serif'
    ]
});

export default typography;



